import './PokemonInfo.css'
import { Link } from 'react-router-dom';
import Card from 'react-bootstrap/Card';
import Button from 'react-bootstrap/Button';

const PokemonInfo = (props) => {
    return (
        <Card className="card infoContainer">
            <div className="left">
                <img className="pokemonInfoImg" src={props.image} alt={props.name}></img>
            </div>
            <div className="right">
                <h1>{props.name}</h1>
                <p>{props.description}</p>
                <div className="PokemonTypes"> <b>TYPE/S:</b>
                {
                    props.type.map((item, i) => {
                        if(i === 0) {
                            return  <Link to={`/types/${item.toLowerCase()}`} key={item}>
                                        <Button variant="outline-info" className=" pokemonTypesBtn m-2 p-2" key={item}> {item} </Button>
                                    </Link> 
                        }
                        else {
                            return  <Link to={`/types/${item.toLowerCase()}`} key={item}>
                                        <Button variant="outline-info" className=" pokemonTypesBtn m-2 p-2" key={item}> {item} </Button>
                                    </Link> 
                        }
                    })
                }
                    <br/><Link to={'/'}>
                        <Button variant="outline-info" className=" backMainBtn m-2 p-2">Back to main</Button>
                    </Link> 
                </div>
            </div>
        </Card>
    );
}
export default PokemonInfo;