import React from 'react';
import './Heroes.css';
import { Link } from 'react-router-dom';

const Heroes = (props) => {
    return (
        <tr>
            <td>
                <p>{props.name}</p>
            </td>
            <td>
                <Link to={`/${props.name}`} ><img className="pokemonImg" src={props.image} alt={props.name}/></Link>
            </td>
            <td>
                {
                    props.type.map((item, i) => {
                        if(i === 0) {
                            return item
                        }
                        else {
                            return ', ' + item
                        }
                    })
                }
            </td>
        </tr>
    );
}
export default Heroes;