import "./App.css";
import pokemon from './Components/Heroes/Images/pokemon.png';
import PokemonTable from './Components/Heroes/PokemonTable';
import Types from './Components/Heroes/Types';
import PokemonInfo from './Components/Heroes/PokemonInfo';
import Header from './Components/Heroes/Header';
import { Container, Col, Jumbotron } from 'react-bootstrap';
import { Route } from 'react-router-dom';
import { connect } from "react-redux";

const App = (props) => {
  return (
    <div className="App">
      <Container fluid className="p-0">
      <Route exact path="/">
        <Header/>
        <Col className="typesContainer d-xl-flex">
          <Jumbotron className="navTypes m-3 col-sm-12 col-md-9 mt-md-3 m-md-auto col-xl-8">
            <h5 className="navText">Choose your</h5>
            <div className="homeImageDiv">
              <img className="homeImage" src="https://upload.wikimedia.org/wikipedia/commons/thumb/9/98/International_Pok%C3%A9mon_logo.svg/1200px-International_Pok%C3%A9mon_logo.svg.png"/>
            </div>
              <br />
            <div className="pickachuOutlineDiv">
              <img className="pickachuOutline" src={pokemon} />
            </div>
          </Jumbotron>
          <Types types={props.types} key={props.types} />
        </Col>
          <br />
      </Route>
      
      { props.types.map((type) => {
          return (
            <Route path={`/types/${type.toLowerCase()}`} key={type}>
              <PokemonTable pokemons={props.pokemons} type={type} key={type}/>
            </Route>
            
          );
        }) 
      }
      { props.pokemons.map((pokemon) => {
          return (
            <Route path={`/${pokemon.name}`} key={pokemon.name}>
            <Header/>
            <div>
                <PokemonInfo name={pokemon.name}
                  description={pokemon.description}
                  image={pokemon.img}
                  key={pokemon.img}
                  pokemon={props.pokemonInfo}
                  type={pokemon.types} />
              </div>
            </Route>
          );
        })
        }
      </Container>
    </div>
  );
}

const mapStateToProps = state => {
  return {
    pokemons: state.pokemons,
    types: state.types,
    pokemonInfo: state.pokemonInfo
  }
}
export default connect(mapStateToProps)(App);