import { Link } from 'react-router-dom';
import DropdownButton from 'react-bootstrap/DropdownButton';
import Dropdown from 'react-bootstrap/Dropdown';
import Card from 'react-bootstrap/Card';

const Types = (props) => {
    return (
        <Card className="pokeTypes">
            <Card.Body>
                <DropdownButton variant="info" id="dropdown-basic-button" title="Select Pokemon Type" size="lg">
                        {
                            props.types.map((type) => {
                                return (
                                <Link className="typeLink" to={`/types/${type.toLowerCase()}`} key={type}>
                                    <Dropdown.Item as="button" className="options"> {type} </Dropdown.Item>
                                </Link>
                                );
                            })
                        }
                </DropdownButton>
            </Card.Body>
        </Card>
           

    );
}
export default Types;