import Heroes from './Heroes';
import { Link } from 'react-router-dom';
import Button from 'react-bootstrap/Button';
import Table from 'react-bootstrap/Table'
import './PokemonTable.css';

let pokemons;

const PokemonTable = (props) => {
   
    if (props.type) {
        pokemons = props.pokemons.filter(pokemon => pokemon.types.includes(props.type));
    }
    return (
        <div>
            <Link to={'/'}>
                <Button variant="outline-info" className=" backMainBtn m-2 p-2">Back to main</Button>
            </Link> 
            <Table responsive striped bordered hover variant="dark" className="heroesTable">
                <thead className="label">
                    <tr>
                        <th>NAME</th>
                        <th>IMAGE</th>
                        <th>TYPE</th>
                    </tr>
                </thead>
                <tbody>
                    {
                        pokemons.map((pokemon) => {
                            return (
                                <Heroes name={pokemon.name}
                                    image={pokemon.img}
                                    type={pokemon.types}
                                    key={pokemon.name} />
                            );
                        })
                    }
                </tbody>
            </Table>
        </div>
    );
}
export default PokemonTable;